@import '~scss/variables';
@import '~scss/custom-properties';

.res-header {
  line-height: 0;
  padding: 15px 5px;

  @media (min-width: $collapse-sidebar-width) {
    padding: 15px 15px 15px 15px;
  }

  .page-title {
    margin: 0 0 .5rem 0 !important;
  }

  small {
    margin-left: 2px;
    line-height: 1;
    color: $extra-bg-color;
    text-transform: none;
  }
}

.res-container {
  display: flex;
  flex-direction: column;
  margin-top: $header-banner-bar-size;
  overflow: auto; // to fix layout in Firefox
}

.res-body, .res-confirm-body {
  padding: 0 .333rem;

  @media (min-width: $screen-lg) {
    padding: 0 1rem;
  }
}

.res-body {
  min-height: 320px;
  height: 100%;
  //max-width: 815px;

  @media (min-width: $screen-lg) {
    padding: 0 1rem 1rem 1rem;
  }

  .res-col-opts {
    flex: 0 1 160px;
    @media (min-width: 400px) {
      flex: 0 1 270px;
    }
  }

  .res-col-time {
    flex: 1 0 170px;
    position: relative;
    margin-left: 3px;

    @media (min-width: 400px) {
      flex: 0 2 400px;
    }

    @media (min-width: $screen-sm) {
      margin-left: 10px;
    }
    @media (min-width: $screen-lg) {
      margin-left: 15px;
    }
  }
}


res-reservation {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 0;

  ff-textarea {
    min-height: 30px;

    .ff-control {
      text-transform: none;
    }
  }

  rc-date-selector .bar {
    font-size: 14px;
    margin-left: 0;

    @media (min-width: $screen-sm) {
      & {
        font-size: 20px;
        margin-left: 5px;
      }
    }
  }

  @media (max-width: 400px) {
    // compact reservation form
    .res-buttons {
      .btn {
        font-size: .85em !important;
        padding-right: 5px !important;
        padding-left: 5px !important;
      }
      ff-btn {
        margin-left: 0 !important;
        margin-right: 0 !important;
      }
    }

    .time-selector .clear-selection {
      padding-right: 0;
    }
  }

  @media (max-width: 360px) {
    // extra compact reservation form
    .res-buttons {
      .btn {
        font-size: .75em !important;
      }
    }
  }
}

// Current member above time selector
.res-current-member {
  flex: 0 0 auto;
  color: var(--brand-color);
  font-size: 14px;
  text-align: center;

  @media (min-width: $screen-sm) {
    font-size: 16px;
  }
}

// Make/Update/Cancel Reservation buttons
.res-buttons {
  margin-bottom: -8px;
  z-index: 3;

  ff-btn {
    margin: 0 5px 8px 0;
  }
}

.res-confirm {
  margin-top: $header-banner-bar-size;
  overflow: auto;

  ff-btn-group {
    max-width: 640px;
    padding-right: 1rem;
  }
}

.res-confirm-body {
  max-width: 640px;

  &, .ff-control, ff-checkbox {
    text-transform: none;
  }
}
