@import '~scss/variables';
@import '~scss/custom-properties';

time-selector {
  min-height: 0;
}

.time-selector {
  display: flex;
  flex-direction: column;

  .slot {
    position: relative;
    height: $time-selector-row-height;
    line-height: 1;
    background-color: $rc-border-light-color;

    &.available {
      cursor: pointer;
      background-color: #fff;
    }

    // Borders between rows
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 1px;
      background: $rc-border-dark-color;
      transition: background 0.1s;
      z-index: 1;
    }

    &.available::before {
      background: $rc-border-dark-color;
    }

    &.available:hover, &.selected {
      &::before { // Borders between rows
        background: var(--rc-reserved-own-color);
      }
    }

    &.reserved {
      cursor: auto;
    }


    // Group of reserved/selected rows
    .reserved-outline, .buffer-outline {
      position: relative;
      top: 0;
      height: 100%;
      margin: 0 0 0 $time-selector-time-col-width;

      .reserved-outline-inner, .buffer-outline-inner {
        display: table;
        position: absolute;
        top: 20px;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 3;
        text-align: center;
        background: var(--rc-reserved-color);
        color: #fff;
        text-transform: uppercase;
        border-radius: 3px;

        span {
          display: table-cell;
          vertical-align: middle;
          font-size: 13px;
        }
      }

      .reserved-outline-inner {
        text-transform: none;
        font-weight: normal;
        z-index: 4;
      }

      .buffer-outline-inner {
        color: var(--text-color);
        background-color: #e7e7e7;
        & > span {
          font-size: .7em;
          opacity: .5;
        }
      }
    }

    .reserved-outline.own .reserved-outline-inner {
      background: var(--rc-reserved-own-color);
      color: var(--rc-reserved-own-text-color);
    }

    // Time marker at left
    .time-cell {
      position: absolute;
      top: 0;
      left: 0;
      width: $time-selector-time-col-width;
      height: 100%;
      background-color: $rc-border-light-color;
      padding-left: 10px;
      transition: color 0.1s;
      z-index: 2;

      &::before {
        content: '';
        position: absolute;
        background: $rc-border-light-color;
        width: 88%;
        height: 80%;
        top: 10%;
        left: 5%;
        border-radius: 3px;
        transition: background 0.1s;
      }

      &::after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        top: 50%;
        left: 94%;
        margin-left: -1px;
        margin-top: -7px;
        border-style: solid;
        border-width: 7px 0 7px 5px;
        border-color: transparent transparent transparent $rc-border-light-color;
        transition: border-color 0.1s;
      }

      &.time-cell-right {
        left: auto;
        right: 0;

        &::before {
          left: 7%;
        }

        &::after {
          left: 0;
          margin-left: 0;
          transform: rotate(180deg);
        }
      }
    }

    &.available .time-cell {
      background-color: #fff;
    }

    // Marker for others reservations
    &.reserved .time-cell, &.reserved:not(.reserved-own):hover .time-cell {
      color: #fff;

      &::before {
        background: var(--rc-reserved-color);
      }
      &::after {
        border-color: transparent transparent transparent var(--rc-reserved-color);
      }
    }
    // Marker for own reservations
    &.available:hover .time-cell, &.selected .time-cell, &.reserved-own .time-cell {
      color: #fff;

      &::before {
        background: var(--rc-reserved-own-color);
      }
      &::after {
        border-color: transparent transparent transparent var(--rc-reserved-own-color);
      }
    }

    .time {
      position: relative;
      display: inline-block;
      line-height: $time-selector-row-height;
    }

    &:not(.available) .time {
      opacity: 0.5;
    }

    sup {
      display: block;
      position: absolute;
      top: auto;
      bottom: 50%;
      left: 50%;
      padding-left: 18px;
      font-size: 10px;
      line-height: 1;
      opacity: 0.75;
    }
  }

  // Hint: Click an open time slot to select a start time
  .hint {
    flex: 0 0 auto;
    padding-bottom: 5px;
    font-size: 12px;
    text-transform: none;
    text-align: center;
    color: $extra-bg-color;
  }

  .clear-selection {
    flex: 0 0 auto;
    padding: 10px 0 0 0;
    text-align: right;
    .btn {
      margin-left: auto;
      font-family: var(--font-family-minor);
      font-size: 12px;
      border-radius: .75rem;
    }
  }

  .slots-wrapper {
    position: relative;
    overflow-y: scroll;
    user-select: none;
  }

  .selected-outline {
    position: absolute;
    width: calc(100% - #{$time-selector-time-col-width});
    margin: 0 0 0 $time-selector-time-col-width;
    z-index: 5;
    overflow: hidden;
  }

  .selected-outline-inner {
    background: transparent;
    overflow: hidden;
    cursor: move;

    width: 100%;
    height: 100%;

    .diagonal-hatch { // SVG element for diagonal hatch pattern
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
    }
    .diagonal-hatch-pattern { // SVG diagonal hatch background pattern
      fill: none;
      stroke: var(--rc-reserved-own-color);
      stroke-width: 1px;
    }
    .diagonal-hatch-rect {
      fill: url(#diagonalHatch) var(--rc-reserved-own-color);
    }
  }

  .selected-from, .selected-to { // From/To selection markers
    position: absolute;
    top: 1px;
    left: 0;
    width: 30%;
    min-width: 32px;
    height: 17px;
    border-radius: 3px;
    background: var(--rc-reserved-own-color);
    color: var(--rc-reserved-own-text-color);
    font-size: 11px;
    line-height: 17px;
    text-transform: uppercase;
    text-align: center;
    cursor: ns-resize;
    z-index: 10;
  }
  .selected-to {
    top: auto;
    bottom: 0;
  }



