.time-selector .slot {
  color: #f2f2f2;

  &::before {
    background-color: currentColor !important;
    transition: color .1s;
  }

  .time-cell {
    transition: color .1s;
    color: inherit !important;
  }

  .time-cell::before {
    background-color: currentColor !important;
  }

  .time-cell::after {
    border-color: transparent transparent transparent currentColor !important;
  }

  &.available {
    color: #f2f2f2;

    &:not(.reserved):hover {
      color: var(--rc-reserved-own-color);
    }
  }

  &.selected {
    color: var(--rc-reserved-own-color);
  }

  &.reserved {
    color: var(--rc-reserved-color);
    &::before {
      background-color: #f2f2f2 !important;
    }
  }

  &.reserved-own {
    color: var(--rc-reserved-own-color);
  }

  .time {
    color: #000;
    transition: color .1s;
  }

  &.reserved {
    .time {
      color: #fff;
    }
  }
  &.selected, &.reserved-own, &.available:not(.reserved):hover {
    .time {
      color: var(--rc-reserved-own-text-color);
    }
  }
}
